  display: flex;
  align-items: center;
  min-width: 270px;
}

.l-menu--link a {
  color: #00cc99;
  text-decoration: none;
  border-bottom-width: 3px;
  border-bottom-style: solid;
  border-bottom-color: transparent;
  display: inline-block;
}

.l-menu--link a:hover {
  border-bottom-color: #00cc99;
}

.l-menu__item--active a {
  border-bottom-color: #00cc99;
}

