  text-decoration: none;
}

.l-communityList a:hover {
  color: #333;
}

.l-communityList--fb a {
  color: #4460A0;
  border-right: 1px #ccc solid;
}

.l-communityList--wt a {
  color: #00AAEC;
}

.l-footer {
  display: flex;
  justify-content: space-between;
}

